<template>
  <div class="auto-info-container">
    <section class="task-creation-section">
      <el-card class="task-creation-card" shadow="hover">
        <template #header>
          <div class="card-header">
            <span></span>
          </div>
        </template>

        <el-form ref="taskForm" :model="formData" :rules="rules" label-width="100px">
          <el-row :gutter="20">
            <el-col :xs="24" :sm="12" :md="12" :lg="12">
              <el-form-item label="" prop="recipient">
                <el-input v-model="formData.recipient" placeholder="" clearable>
                  <template #prefix>
                    <el-icon>
                      <User />
                    </el-icon>
                  </template>
                </el-input>
                <div class="el-form-item__tip"></div>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="12" :md="12" :lg="12">
              <el-form-item label="" prop="sendTime">
                <el-date-picker v-model="formData.sendTime" type="datetime" placeholder=""
                  value-format="YYYY-MM-DDTHH:mm" :default-value="defaultDateTime" style="width: 100%" />
                <div class="el-form-item__tip"></div>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="12" :md="12" :lg="12">
              <el-form-item label="" prop="repeatType">
                <el-select v-model="formData.repeatType" placeholder="" style="width: 100%">
                  <el-option v-for="option in repeatOptions" :key="option.value" :label="option.label"
                    :value="option.value" />
                </el-select>
                <el-form-item v-if="formData.repeatType === 'custom'" prop="repeatDays" :rules="[{
                  validator: (_, value, callback) => {
                    if (formData.repeatType === 'custom' && (!value || value.length === 0)) {
                      callback(new Error(''))
                    } else {
                      callback()
                    }
                  },
                  trigger: 'change'
                }]">
                  <p class="el-form-item__tip"></p>
                  <el-checkbox-group v-model="formData.repeatDays">
                    <el-checkbox v-for="day in daysOfWeek" :key="day.value" :label="day.value">
                      {{ day.label }}
                    </el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
                <div class="el-form-item__tip"></div>
              </el-form-item>
            </el-col>

            <el-col :span="24">
              <el-form-item label="" prop="messageContent">
                <el-input v-model="formData.messageContent" type="textarea" placeholder="" :rows="4"
                  show-word-limit maxlength="500" />
                <div class="el-form-item__tip">
                  <p><strong>D:\\.pdf</strong></p>
                  <p> <strong>SendEmotion:1</strong> 1 <strong>SendEmotion:2,3,4</strong> 234</p>
                </div>
              </el-form-item>

            </el-col>
          </el-row>

          <el-form-item>
            <el-button type="default" @click="resetForm"></el-button>
            <el-button type="primary" @click="submitForm">
              
            </el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </section>

    <section class="task-list-section">
      <el-card>
        <template #header>
          <div class="card-header">
            <div class="header-title">
              <span></span>
              <span class="task-count">{{ tasks.length }}</span>
            </div>
            <div class="task-actions">
              <el-button-group>
                <el-button @click="refreshTasks">
                  <el-icon>
                    <Refresh />
                  </el-icon>
                </el-button>
                <el-button @click="importTasks">
                  
                </el-button>
                <el-button :disabled="tasks.length === 0" @click="exportTasks">
                  
                </el-button>
                <el-button :disabled="tasks.length === 0" @click="clearAllTasks" type="danger">
                  
                </el-button>
                <el-button :disabled="tasks.length === 0" @click="toggleTaskScheduler" :type="isSchedulerRunning ? 'danger' : 'primary'">
                  {{ isSchedulerRunning ? '' : '' }}
                </el-button>
              </el-button-group>
              <input type="file" ref="fileInput" style="display: none;" accept=".xlsx" @change="handleFileImport">
            </div>
          </div>
        </template>

        <el-table v-if="tasks.length > 0" :data="sortedTasks" style="width: 100%" stripe fit
          :row-class-name="tableRowClassName">
          <el-table-column prop="recipient" label="" min-width="20px" />
          <el-table-column prop="messageContent" label="" min-width="20px">
            <template #default="{ row }">
              <el-tooltip :content="row.filePath || row.messageContent" placement="top">
                <div class="message-content">
                  {{ row.filePath || row.messageContent }}
                </div>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column prop="sendTime" label="" min-width="15px">
            <template #default="{ row }">
              {{ formatDateTime(row.sendTime) }}
            </template>
          </el-table-column>
          <el-table-column label="" min-width="20px">
            <template #default="{ row }">
              {{ getRepeatText(row.repeatType, row.repeatDays) }}
            </template>
          </el-table-column>
          <el-table-column label="" min-width="10px" fixed="right">
            <template #default="{ row }">
              <el-tag :type="row.status === 'pending' ? 'warning' : row.status === 'completed' ? 'success' : 'danger'" size="small">
                {{ row.status === 'pending' ? '' : row.status === 'completed' ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="20px" v-if="tasks.some(task => task.errorMessage)">
            <template #default="{ row }">
              <el-tooltip v-if="row.errorMessage" :content="row.errorMessage" placement="top">
                <el-tag type="danger" size="small">
                  <el-icon><Warning /></el-icon>
                  
                </el-tag>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="16px" fixed="right">
            <template #default="{ row }">
              <div class="operation-buttons">
                <el-button type="primary" size="small" @click="editTask(row.id)" :icon="Edit"></el-button>
                <el-button type="danger" size="small" @click="deleteTask(row.id)" :icon="Delete"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>

        <el-empty v-else description="">
        </el-empty>
      </el-card>
    </section>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, onUnmounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { User, Delete, Refresh, Edit, Warning } from '@element-plus/icons-vue'
import * as XLSX from 'xlsx'

const taskForm = ref()
const formData = reactive({
  recipient: '',
  sendTime: '',
  repeatType: 'none',
  repeatDays: [],
  messageContent: ''
})

const rules = {
  recipient: [
    { required: true, message: '', trigger: 'blur' },
    {
      validator: (_, value, callback) => {
        if (!value || !value.trim()) {
          callback(new Error(''))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  sendTime: [
    { required: true, message: '', trigger: 'change' }
  ],
  messageContent: [
    { 
      validator: (_, value, callback) => {
        // 
        callback()
      }, 
      trigger: 'blur' 
    }
  ],
  repeatType: [
    { required: true, message: '', trigger: 'change' }
  ]
}

const tasks = ref([])
const isSchedulerRunning = ref(false)

const repeatOptions = [
  { label: '', value: 'none' },
  { label: '', value: 'daily' },
  { label: '', value: 'workday' },
  { label: '', value: 'holiday' },
  { label: '', value: 'custom' }
]

const daysOfWeek = [
  { label: '', value: '1' },
  { label: '', value: '2' },
  { label: '', value: '3' },
  { label: '', value: '4' },
  { label: '', value: '5' },
  { label: '', value: '6' },
  { label: '', value: '0' }
]

const defaultDateTime = computed(() => {
  const now = new Date()
  now.setMinutes(now.getMinutes() + 30)
  return now
})

const sortedTasks = computed(() => {
  return [...tasks.value].sort((a, b) => new Date(a.sendTime) - new Date(b.sendTime))
})

const resetForm = () => {
  taskForm.value?.resetFields()
}

const submitForm = async () => {
  try {
    await taskForm.value.validate()
    
    // 
    if (!formData.messageContent) {
      ElMessage.error('')
      return
    }

    // 
    const tasksToCreate = [{
      ...formData,
      status: 'pending',
      createdAt: new Date().toISOString()
    }]
    
    // 
    let successCount = 0
    for (const taskData of tasksToCreate) {
      const response = await fetch('/api/tasks', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(taskData)
      })
      
      if (response.ok) {
        const newTask = await response.json()
        tasks.value.push(newTask)
        successCount++
      }
    }
    
    if (successCount === tasksToCreate.length) {
      ElMessage.success(` ${successCount} `)
      resetForm()
    } else {
      ElMessage.error(` ${successCount}/${tasksToCreate.length} `)
    }
  } catch (error) {
    if (!error.errors) {
      console.error(':', error)
      ElMessage.error('')
    }
  }
}

const tableRowClassName = ({ row }) => {
  return row.status === 'pending' ? 'task-pending' : 'task-completed'
}

const editTask = async (taskId) => {
  const task = tasks.value.find(t => t.id === taskId)
  if (task) {
    formData.recipient = task.recipient
    formData.sendTime = task.sendTime
    formData.repeatType = task.repeatType
    formData.repeatDays = task.repeatDays
    formData.messageContent = task.messageContent


    // 
    try {
      const response = await fetch(`/api/tasks/${taskId}`, {
        method: 'DELETE'
      })

      if (response.ok) {
        tasks.value = tasks.value.filter(t => t.id !== taskId)
        ElMessage({ message: '', type: 'info' })
      } else {
        ElMessage.error('')
      }
    } catch (error) {
      console.error(':', error)
      ElMessage.error('')
    }
  }
}

const deleteTask = async (taskId) => {
  ElMessageBox.confirm('', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
  }).then(async () => {
    try {
      const response = await fetch(`/api/tasks/${taskId}`, {
        method: 'DELETE'
      })

      if (response.ok) {
        tasks.value = tasks.value.filter(task => task.id !== taskId)
        ElMessage.success('')
      } else {
        ElMessage.error('')
      }
    } catch (error) {
      console.error(':', error)
      ElMessage.error('')
    }
  }).catch(() => { })
}

const clearAllTasks = async () => {
  ElMessageBox.confirm('', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning',
    confirmButtonClass: 'el-button--danger'
  }).then(async () => {
    try {
      const response = await fetch('/api/tasks', {
        method: 'DELETE'
      })

      if (response.ok) {
        tasks.value = []
        ElMessage.success('')
      } else {
        const errorText = await response.text()
        ElMessage.error(`: ${errorText || ''}`)
      }
    } catch (error) {
      console.error(':', error)
      ElMessage.error('')
    }
  }).catch(() => { })
}

const formatDateTime = (dateString) => {
  if (!dateString) return ''
  const date = new Date(dateString)
  if (isNaN(date.getTime())) return dateString
  return `${date.getFullYear()}-${padZero(date.getMonth() + 1)}-${padZero(date.getDate())} ${padZero(date.getHours())}:${padZero(date.getMinutes())}`
}

const padZero = (num) => {
  return num < 10 ? '0' + num : num
}

const refreshTasks = async () => {
  try {
    const response = await fetch('/api/tasks')
    if (response.ok) {
      const data = await response.json()
      tasks.value = data
      
      // 
      const pendingTasks = data.filter(task => task.status === 'pending')
      if (pendingTasks.length === 0 && isSchedulerRunning.value) {
        // 
        isSchedulerRunning.value = false
        ElMessage.success('')
      }
    } else {
      const errorText = await response.text()
      ElMessage.error(`: ${errorText || ''}`)
    }
  } catch (error) {
    ElMessage.error(`: ${error.message || ''}`)
  }
}

// 
const exportTasks = () => {
  if (tasks.value.length === 0) {
    ElMessage.warning('')
    return
  }

  // 
  const exportData = tasks.value.map(task => ({
    '': formatDateTime(task.sendTime),
    '': task.recipient,
    '': task.filePath || task.messageContent,
    '': getRepeatText(task.repeatType, task.repeatDays)
  }))

  // 
  const ws = XLSX.utils.json_to_sheet(exportData)
  const wb = XLSX.utils.book_new()
  XLSX.utils.book_append_sheet(wb, ws, '')

  // Excel
  XLSX.writeFile(wb, `tasks_export_${new Date().toISOString().slice(0, 10)}.xlsx`)

  ElMessage.success('')
}

// 
const fileInput = ref(null)

const importTasks = () => {
  fileInput.value?.click()
}

const handleFileImport = async (event) => {
  const file = event.target.files[0]
  if (!file) return

  try {
    const reader = new FileReader()
    reader.onload = async (e) => {
      try {
        // Excel
        const data = new Uint8Array(e.target.result)
        const workbook = XLSX.read(data, { type: 'array' })
        const firstSheetName = workbook.SheetNames[0]
        const worksheet = workbook.Sheets[firstSheetName]
        const importedData = XLSX.utils.sheet_to_json(worksheet)

        // 
        if (!Array.isArray(importedData)) {
          throw new Error('')
        }

        // Excel
        const validTasks = importedData.map(item => {
          // 
          let repeatType = 'none'
          let repeatDays = []
          const repeatText = item[''] || ''

          if (repeatText.includes('')) {
            repeatType = 'daily'
          } else if (repeatText.includes('')) {
            repeatType = 'workday'
          } else if (repeatText.includes('')) {
            repeatType = 'holiday'
          } else if (repeatText.includes('')) {
            repeatType = 'custom'
            // 
            const dayMap = {
              '': '0', '': '1', '': '2', '': '3',
              '': '4', '': '5', '': '6'
            }
            Object.keys(dayMap).forEach(day => {
              if (repeatText.includes(day)) {
                repeatDays.push(dayMap[day])
              }
            })
          }

          return {
            recipient: item[''] || '',
            sendTime: item[''] ? new Date(item['']).toISOString() : '',
            messageContent: item[''] || '',
            repeatType,
            repeatDays
          }
        }).filter(task => {
          // 
          return task.recipient && task.sendTime && (task.messageContent || task.filePath)
        })

        // 
        const response = await fetch('/api/tasks/import', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(validTasks)
        })

        if (response.ok) {
          ElMessage.success(` ${validTasks.length} `)
          refreshTasks()
        } else {
          throw new Error('')
        }
      } catch (error) {
        ElMessage.error(`: ${error.message}`)
      }
    }
    reader.readAsArrayBuffer(file)
  } catch (error) {
    ElMessage.error(`: ${error.message}`)
  }

  // 
  event.target.value = ''
}



const getRepeatText = (repeatType, repeatDays) => {
  const dayMap = {
    '0': '', '1': '', '2': '', '3': '',
    '4': '', '5': '', '6': ''
  }

  switch (repeatType) {
    case 'none': return ''
    case 'daily': return ''
    case 'workday': return ''
    case 'holiday': return ''
    case 'custom': return `: ${repeatDays?.map(day => dayMap[day]).join(', ')}`
    default: return ''
  }
}

const toggleTaskScheduler = async () => {
  try {
    if (isSchedulerRunning.value) {
      // 
      const response = await fetch('/api/task-scheduler/stop', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      
      if (response.ok) {
        isSchedulerRunning.value = false
        ElMessage.success('')
      } else {
        const errorData = await response.json()
        ElMessage.error(`: ${errorData.error || ''}`)
      }
    } else {
      // 
      const response = await fetch('/api/task-scheduler/start', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      
      if (response.ok) {
        isSchedulerRunning.value = true
        ElMessage.success('')
      } else {
        const errorData = await response.json()
        ElMessage.error(`: ${errorData.error || ''}`)
      }
    }
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  }
}

const checkSchedulerStatus = async () => {
  try {
    const response = await fetch('/api/task-scheduler/status')
    if (response.ok) {
      const data = await response.json()
      isSchedulerRunning.value = data.isRunning
    }
  } catch (error) {
    console.error(':', error)
  }
}

onMounted(() => {
  refreshTasks()
  checkSchedulerStatus()
  
  // 
  const schedulerStatusInterval = setInterval(() => {
    checkSchedulerStatus()
    refreshTasks() // 
  }, 3000) // 3
  
  // 
  onUnmounted(() => {
    clearInterval(schedulerStatusInterval)
  })
})
</script>

<style scoped>
:root {
  --primary-color: #1e40af;
  --secondary-color: #3b82f6;
  --accent-color: #2563eb;
  --light-color: #f8fafc;
  --dark-color: #1e293b;
  --text-primary: #0f172a;
  --text-secondary: #3b82f6;
  --success-color: #8b5cf6;
  --warning-color: #f59e0b;
  --danger-color: #ef4444;
  --border-color: #e2e8f0;
}


.task-pending .el-table__cell {
  background-color: rgba(59, 130, 246, 0.1);
}

.task-completed .el-table__cell {
  background-color: rgba(16, 185, 129, 0.1);
}


.task-creation-card,
.task-list-section .el-card {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  overflow: hidden;
  background-color: white;
}

.task-creation-card:hover,
.task-list-section .el-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px 0 rgba(0, 0, 0, 0.08), 0 2px 4px 0 rgba(0, 0, 0, 0.04);
  border-color: var(--primary-color);
}


.el-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(30, 64, 175, 0.2);
}

.el-button {
  transition: all 0.2s ease;
}

.el-button--primary {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.el-button--primary:hover {
  background-color: #1e3a8a;
  border-color: #1e3a8a;
}

.auto-info-container {
  padding: 0;
  max-width: 100vw;
  margin: 0 auto;
  background-color: var(--light-color);
  min-height: 100vh;
}

.task-creation-section {
  margin-bottom: 24px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
}

.card-header .span {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.header-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 16px;
  color: var(--text-primary);
}

.task-count {
  font-size: 12px;
  color: var(--text-secondary);
  background-color: var(--light-color);
  padding: 2px 8px;
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.task-actions {
  display: flex;
  gap: 10px;
  padding: 5px 0;
}

.message-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.operation-buttons {
  display: flex;
  gap: 6px;
  justify-content: center;
}

.operation-buttons .el-button {
  padding: 4px 8px;
  font-size: 12px;
}

.custom-days {
  margin-top: 10px;
  padding: 12px;
  background-color: white;
  border-radius: 6px;
  border: 1px solid var(--border-color);
}

.el-form-item__tip {
  font-size: 12px;
  color: var(--text-secondary);
  margin-top: 4px;
}


.el-table {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.08);
}

.el-table th {
  background-color: rgba(30, 64, 175, 0.05);
  font-weight: 600;
  color: var(--text-primary);
  padding: 12px 0;
}

.el-table td {
  padding: 12px 0;
  border-bottom: 1px solid var(--border-color);
}

.el-table tr:last-child td {
  border-bottom: none;
}

.el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: rgba(59, 130, 246, 0.08);
}


.el-table__fixed-right {
  box-shadow: -2px 0 6px rgba(0, 0, 0, 0.05);
}


.el-tag--info {
  background-color: rgba(59, 130, 246, 0.1);
  color: var(--secondary-color);
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.el-tag--success {
  background-color: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
  border: 1px solid rgba(16, 185, 129, 0.3);
}

@media (max-width: 768px) {
  .card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .task-actions {
    width: 100%;
  }

  .el-button-group {
    display: flex;
    width: 100%;
  }

  .el-button-group .el-button {
    flex: 1;
  }
}
</style>

